<template>
  <div class="col-9 pt-3 bg-muted d-flex flex-column">
    <loading-overlay :loading="loadingOverlay" class="flex-fill overflow-h">
    <div class="row">
      <div v-if="showChannel" class="col-4">
        <div>
          <small class="font-weight-bold">Channel</small>
          <multiselect
            class
            v-model="channel"
            label="name"
            track-by="id"
            :options="channelList"
            :maxHeight="200"
            :allow-empty="false"
            :show-labels="false"
          ></multiselect>
        </div>
      </div>
      <div v-if="showStep" class="col-4">
        <small class="font-weight-bold">Customer Journey Step</small>
        <multiselect
          v-model="step"
          label="name"
          track-by="id"
          :options="stepList"
          :maxHeight="200"
          :allow-empty="false"
          :show-labels="false"
        ></multiselect>
      </div>
      <!-- <div class="col-3">
        <small class="font-weight-bold">Answerset Tags</small>
        <multiselect
          v-model="tag"
          label="name"
          track-by="id"
          :options="tagList"
          :maxHeight="200"
          :show-labels="false"
        ></multiselect>
      </div> -->
      <div class="col-4">
        <small class="font-weight-bold">Keyword</small>
        <input v-model="searchText" class="form-control br border-0" />
      </div>
    </div>
    <div class="row">
      <div class="col-12 p-2">
        <div class="text-align-right">
          <button class="btn btn-primary mb-3" @click="showAddModal">Add Answer Set</button>
        </div>
        <div v-if="loading">
          <vue-content-loading :height="90">
            <rect x="0" y="10" width="50" height="15" />
            <rect x="60" y="10" width="80" height="15" />
            <rect x="0" y="40" width="300" height="10" />
            <rect x="0" y="55" width="300" height="10" />
            <rect x="0" y="70" width="300" height="10" />
          </vue-content-loading>
          <vue-content-loading :height="90">
            <rect x="0" y="10" width="50" height="15" />
            <rect x="60" y="10" width="80" height="15" />
            <rect x="0" y="40" width="300" height="10" />
            <rect x="0" y="55" width="300" height="10" />
            <rect x="0" y="70" width="300" height="10" />
          </vue-content-loading>
          <vue-content-loading :height="90">
            <rect x="0" y="10" width="50" height="15" />
            <rect x="60" y="10" width="80" height="15" />
            <rect x="0" y="40" width="300" height="10" />
            <rect x="0" y="55" width="300" height="10" />
            <rect x="0" y="70" width="300" height="10" />
          </vue-content-loading>
        </div>
        <div v-else class="d-flex flex-column p-2">
          <div class="flex-fill m-0 row overflow-h" style="height: 78vh;">
            <div class="col-6 scroll pl-0">
              <div
                :key="l.ID"
                v-for="l in list"
                @click="showSelected(l)"
                :class="activeSelected(l.ID) ? `answersetActive` : `answerset`"
              >
                <!-- <span class="pill px-2 py-1 m-1" style :key="t.Name" v-for="t in l.tags">{{t.Name}}</span> -->
                <!-- <br /> -->
                <span>{{l.Title}}</span>
                <br />
                <div class="d-flex flex-row-reverse">
                  <div v-for="(item,m) in JSON.parse(l.Messages)" :key="m">
                    <span class="pill pill-sm" style v-if="item.type == 'text'">Text</span>
                    <span class="pill pill-sm" style v-if="item.type == 'image'">Image</span>
                    <span class="pill pill-sm" style v-if="item.type == 'card'">Card</span>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-6 answerset" v-if="selected">
              <h4 style="float:left">Preview</h4>
              <div v-for="l in activeAnsset" :key="l.ID">
                <div style="height: 68vh;padding-top:10px;">
                  <div
                    v-for="(item,m) in JSON.parse(l.Messages)"
                    :key="m"
                    class="test-width test-margin d-flex align-items-center justify-content-end mt-3"
                  >
                    <span v-if="item.type == 'text'" class="answerset-text-local">{{item.text}}</span>

                    <div v-if="item.type == 'image'">
                      <div class="single-pic pointer" v-if="(item.url).length == 1">
                        <img :src="imgUrl(item.url[0])" />
                      </div>
                      <div class="multi-pic row m-0" v-else-if="(item.url).length > 1">
                        <div
                          v-for="(img_url,img_index) in item.url"
                          :key="img_index"
                          class="col-md-4 mt-0 mb-1 p-0 mr-1 d-inline pointer"
                        >
                          <div :style="'background-image: url('+imgUrl(img_url)+')'"></div>
                        </div>
                      </div>
                    </div>

                    <div v-if="item.type == 'card'">
                      <div class="card" style="width: 15rem;">
                        <div
                          v-if="item.cardContent.photo!=''"
                          v-bind:style="{ backgroundImage: 'url(' + imgUrl(item.cardContent.photo) + ')' }"
                          class="d-flex flex-column add-image-div with-photo"
                        ></div>
                        <div class="card-body body-padding">
                          <h5
                            v-if="item.cardContent.title"
                            class="card-title"
                          >{{item.cardContent.title}}</h5>
                          <p
                            v-if="item.cardContent.text"
                            class="card-text"
                          >{{item.cardContent.text}}</p>
                          <div
                            v-if="item.cardContent.buttons.length>0"
                            class="d-flex flex-column align-items-center"
                          >
                            <div v-for="(btn,btnIndex) in item.cardContent.buttons" :key="btnIndex">
                              <button
                                class="btn btn-primary btn-sm mb-2 cardBtn"
                                style="cursor:default !important;"
                              >{{btn.text}}</button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="d-flex justify-content-center pt-2" style="height: 10vh;">
                  <button
                    class="btn btn-danger m-2"
                    style="border-radius:18px;margin-top:0px;height:40px;width:90px;"
                    @click.prevent="deleteMsg(l)"
                  >Delete</button>
                  <button
                    class="btn btn-primary m-2"
                    style="border-radius:18px;margin-top:0px;height:40px;width:90px;"
                    @click="showEditModal(l)"
                  >Edit</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    </loading-overlay>
    <modal name="add" height="auto">
      <div class="p-2 text-left">
        <div class="my-2">
          <span class="text-muted">Title</span>
          <input v-model="title" class="form-control" />
        </div>
        <div v-if="showChannel" class="my-2">
          <span class="text-muted">Channel</span>
          <multiselect
            :disabled="formSubmitting"
            v-model="channels"
            label="name"
            track-by="id"
            :options="channelList"
            :multiple="true"
            :maxHeight="200"
            :show-labels="false"
          ></multiselect>
        </div>

        <div v-if="showStep" class="my-2">
          <span class="text-muted">Customer Journey Step</span>
          <multiselect
            :disabled="formSubmitting"
            v-model="steps"
            label="name"
            track-by="id"
            :options="stepList"
            :maxHeight="200"
            :allow-empty="false"
            :show-labels="false"
          ></multiselect>
        </div>
<!--
        <div class="my-2">
          <span class="text-muted">Tags</span>
          <multiselect
            :disabled="formSubmitting"
            v-model="tags"
            tag-placeholder="Add this as new tag"
            placeholder
            label="name"
            track-by="id"
            :options="tagList"
            :multiple="true"
            :maxHeight="200"
            :show-labels="false"
          ></multiselect>
        </div> -->

        <div class="my-2">
          <div v-if="!activeAnswerset.ID">
            <div
              v-if="getAnswerSet.length>0"
              style="max-height:250px;overflow-y:auto;margin:5px;"
              ref="answersetDiv"
            >
              <div
                v-for="(item,index) in getAnswerSet"
                :key="index"
                class="d-flex flex-column mb-2"
              >
                <div v-if="item.type=='text'" class="d-flex align-items-center justify-content-end">
                  <textarea
                    v-model="item.text"
                    :disabled="formSubmitting"
                    rows="3"
                    cols="30"
                    ref="input"
                    class="form-control mb-2 text-box"
                    placeholder="Text Here"
                  ></textarea>
                  <a class="closeBtn ml-4 mr-3" @click="remove(index)">
                    <i v-tooltip="'Remove'" class="lar la-times-circle"></i>
                  </a>
                </div>

                <div
                  v-if="item.type =='image'"
                  class="test-margin d-flex align-items-center justify-content-end mb-3"
                >
                  <!-- <img :src="imgUrl(item.url)" class="show-img"/>  -->

                  <div class="single-pic pointer" v-if="(item.url).length == 1">
                    <img :src="imgUrl(item.url[0])" />
                  </div>

                  <div class="test-width multi-pic row m-0" v-else-if="(item.url).length > 1 ">
                    <div
                      v-for="(img_url,img_index) in item.url"
                      :key="img_index"
                      class="col-md-4 mt-0 mb-1 p-0 mr-1 d-inline pointer"
                    >
                      <div :style="'background-image: url('+ imgUrl(img_url) +')'"></div>
                    </div>
                  </div>

                  <a class="closeBtn ml-4 mr-3" @click="remove(index)">
                    <i v-tooltip="'Remove'" class="lar la-times-circle"></i>
                  </a>
                </div>

                <div
                  v-if="item.type == 'card'"
                  class="d-flex align-items-center justify-content-end mb-3"
                >
                  <div class="card" style="width: 15rem;">
                    <div
                      v-if="item.cardContent.photo!=''"
                      v-bind:style="{ backgroundImage: 'url(' + item.cardContent.photo + ')' }"
                      class="d-flex flex-column add-image-div with-photo"
                    ></div>
                    <div class="card-body body-padding">
                      <h5
                        v-if="item.cardContent.title"
                        class="card-title"
                      >{{item.cardContent.title}}</h5>
                      <p v-if="item.cardContent.text" class="card-text">{{item.cardContent.text}}</p>
                      <div
                        v-if="item.cardContent.buttons.length>0"
                        class="d-flex flex-column align-items-center"
                      >
                        <div v-for="(btn,btnIndex) in item.cardContent.buttons" :key="btnIndex">
                          <button class="btn btn-primary btn-sm mb-2 cardBtn">{{btn.text}}</button>
                        </div>
                      </div>
                    </div>
                  </div>
                  <a class="closeBtn ml-4 mr-3" @click="remove(index)">
                    <i v-tooltip="'Remove'" class="lar la-times-circle"></i>
                  </a>
                </div>
              </div>
            </div>
          </div>

          <div v-else style="max-height:250px;overflow-y:auto;margin:5px;" ref="activeAnswersetDiv">
            <div v-for="(msg,i) in getActiveAnswersetMsg" :key="i" class="d-flex flex-column mb-2">
              <div v-if="msg.type=='text'" class="d-flex align-items-center justify-content-end">
                <textarea
                  v-model="msg.text"
                  :disabled="formSubmitting"
                  rows="3"
                  cols="30"
                  ref="input"
                  class="form-control mb-2 text-box"
                  placeholder="Text Here"
                ></textarea>
                <a class="closeBtn ml-4 mr-3" @click="remove(i)">
                  <i v-tooltip="'Remove'" class="lar la-times-circle"></i>
                </a>
              </div>
              <div
                v-if="msg.type == 'image'"
                class="test-margin d-flex align-items-center justify-content-end mb-3"
              >
                <div class="single-pic pointer" v-if="(msg.url).length == 1">
                  <img :src="imgUrl(msg.url[0])" />
                </div>

                <div class="test-width multi-pic row m-0" v-else-if="(msg.url).length > 1 ">
                  <div
                    v-for="(img_url,img_index) in msg.url"
                    :key="img_index"
                    class="col-md-4 mt-0 mb-1 p-0 mr-1 d-inline pointer"
                  >
                    <div :style="'background-image: url('+ imgUrl(img_url) +')'"></div>
                  </div>
                </div>

                <a class="closeBtn ml-4 mr-3" @click="remove(i)">
                  <i v-tooltip="'Remove'" class="lar la-times-circle"></i>
                </a>
              </div>
              <div v-if="msg.type == 'card'">
                <div class="d-flex align-items-center justify-content-end">
                  <div class="card" style="width: 15rem;">
                    <div
                      v-if="msg.cardContent.photo!=''"
                      v-bind:style="{ backgroundImage: 'url(' + imgUrl(msg.cardContent.photo) + ')' }"
                      class="d-flex flex-column add-image-div with-photo"
                    ></div>
                    <div class="card-body body-padding">
                      <h5 v-if="msg.cardContent.title" class="card-title">{{msg.cardContent.title}}</h5>
                      <p v-if="msg.cardContent.text" class="card-text">{{msg.cardContent.text}}</p>
                      <div
                        v-if="msg.cardContent.buttons.length>0"
                        class="d-flex flex-column align-items-center"
                      >
                        <div v-for="(btn,btnIndex) in msg.cardContent.buttons" :key="btnIndex">
                          <button
                            class="btn btn-primary btn-sm mb-2 cardBtn"
                            style="cursor:default !important;"
                          >{{btn.text}}</button>
                        </div>
                      </div>
                    </div>
                  </div>

                  <a class="closeBtn ml-4 mr-3" @click="remove(i)">
                    <i v-tooltip="'Remove'" class="lar la-times-circle"></i>
                  </a>
                </div>
              </div>
            </div>
          </div>
          <div class="d-flex align-items-center justify-content-end mb-3">


            <button v-if="answerSet.length < 1" @click="addNewText()" class="btn btn-primary btn-sm mr-3">Add New Text</button>
            <!-- <button @click="addNewImage()" class="btn btn-primary btn-sm mr-3">Add New Image</button> -->
            <!-- <input
              @change="onChange"
              type="file"
              accept="image/*"
              style="display:none;"
              ref="fileInput"
              multiple
            /> -->
            <!-- <button @click="addNewCard()" class="btn btn-primary btn-sm">Add New Card</button> -->
          </div>

          <div class="d-flex mt-2">
            <spinner v-if="formSubmitting" class="mr-5 my-2" />
            <div v-else class="w-100 d-flex flex-row-reverse justify-content-between">
              <button
                v-if="this.activeAnswerset.ID"
                @click="submitEdit"
                class="btn btn-primary"
              >Update</button>
              <button v-else @click="submitAdd" class="btn btn-primary">Submit Template</button>
              <button
                v-if="activeAnswerset.ID"
                @click.prevent="confirmDel"
                class="btn btn-link text-danger"
              >Remove</button>
            </div>
          </div>
        </div>
      </div>
    </modal>
    <v-dialog />
    <!-- Card Modal -->
    <modal name="cardModal" height="auto">
      <div v-if="getCard" class="d-flex justify-content-around align-items-center p-2 text-left">
        <div>
          <div class="card" style="width: 15rem;">
            <div
              v-if="getCard.photo == ''"
              class="d-flex flex-column add-image-div"
              @mouseover="mouseHoverinImgDiv"
              @mouseleave="mouseLeaveinImgDiv"
            >
              <div
                @click="addCardImage"
                class="d-flex flex-column align-items-center"
                style="margin-top: 3rem;margin-left:5rem;position:absolute;"
              >
                <i class="las la-image"></i>
                <span>Add Image</span>
              </div>
              <input
                @change="onCardImageChange"
                type="file"
                accept="image/*"
                style="display:none;"
                ref="cardImageInput"
              />
            </div>

            <div
              v-if="getCard.photo!=''"
              v-bind:style="{ backgroundImage: 'url(' + getCard.photo + ')' }"
              @mouseover="mouseHoverinImgDiv"
              @mouseleave="mouseLeaveinImgDiv"
              class="d-flex flex-column add-image-div with-photo"
            >
              <div class="d-flex justify-content-end">
                <a v-if="imgHover" @click="removeCardImage" class="closeBtn closeImageBtn">
                  <i v-tooltip="'Remove'" class="lar la-times-circle"></i>
                </a>
              </div>
            </div>
            <div class="card-body body-padding">
              <input
                v-model="getCard.title"
                :class="[isTitleActive ? 'card-input-title mb-1 card-input with-background' : 'card-input-title mb-1 card-input']"
                @mouseenter="isTitleActive = true"
                @mouseleave="isTitleActive = false"
              />

              <textarea
                v-model="getCard.text"
                :class="[isTextActive ? 'card-textarea card-input with-background' : 'card-textarea card-input']"
                @mouseenter="isTextActive = true"
                @mouseleave="isTextActive = false"
              />

              <div v-if="getButtonInCard.length>0" class="d-flex flex-column align-items-center">
                <div v-for="(btn,cardBtnIndex) in getButtonInCard" :key="cardBtnIndex">
                  <!-- <input v-model="btn.text" readonly="readonly" class="btn btn-primary btn-sm mb-1 cardBtn"/> -->
                  <v-popover offset="2" placement="right">
                    <button class="btn btn-primary btn-sm mb-2 cardBtn">{{btn.text}}</button>
                    <template slot="popover">
                      <div style="padding:1.25rem;background: #fff;">
                        <div class="d-flex justify-content-center mb-3">
                          <span class="span-font">Edit Button</span>
                        </div>
                        <div class="row mb-1">
                          <div class="col-md-4 text-align-left">
                            <span class="span-font">Text</span>
                          </div>
                          <div class="col-md-8">
                            <input v-model="btn.text" class="form-control" type="text" />
                          </div>
                        </div>
                        <div class="row mb-1">
                          <div class="col-md-4 text-align-left">
                            <span class="span-font">Type</span>
                          </div>
                          <div class="col-md-8">
                            <textarea
                              v-model="btn.type"
                              class="form-control modifyButton-textarea"
                            />
                          </div>
                        </div>
                        <div class="row mb-1">
                          <div class="col-md-4 text-align-left">
                            <span class="span-font">Value</span>
                          </div>
                          <div class="col-md-8">
                            <textarea
                              v-model="btn.value"
                              class="form-control modifyButton-textarea"
                            />
                          </div>
                        </div>
                        <div class="row mb-1 mt-3" style="justify-content:center;">
                          <button v-close-popover="true" class="btn btn-primary btn-sm mr-3">Done</button>
                          <button
                            @click="removeCardBtn(cardBtnIndex)"
                            class="btn btn-secondary btn-sm"
                          >Delete</button>
                        </div>
                      </div>
                    </template>
                  </v-popover>
                </div>
              </div>
            </div>
            <ul v-if="getButtonInCard.length<3" class="list-group list-group-flush addButtonBorder">
              <a
                @click="addNewButtonInCard"
                :class="[isAddButtonActive ? 'list-group-item addBtn addBtnActive' : 'list-group-item addBtn']"
                @mouseenter="isAddButtonActive = true"
                @mouseleave="isAddButtonActive = false"
              >+ Add New Button</a>
            </ul>
          </div>
        </div>
        <div>
          <button @click="createCard()" class="btn btn-primary">Create Card</button>
        </div>
      </div>
    </modal>
    <v-dialog />
  </div>
</template>
<script>
// import { TippyComponent } from "vue-tippy";
import axios from "axios";
import VueContentLoading from "vue-content-loading";
import Spinner from "vue-simple-spinner";
import LoadingOverlay from "@/components/Loaders/LoadingOverlay";
export default {
  components: {
    // TippyComponent,
    VueContentLoading,
    Spinner,
    LoadingOverlay,
  },
  props:{
    showChannel: {
      default: false
    },
    showStep: {
      default: false
    }
  },
  data() {
    return {
      answerSet: [],
      activeAnswerset: {},
      activeAnswersetMsg: [],
      activeAnsset: [],
      channel: [],
      channels: [],
      channelList: [],
      steps: [],
      step: [],
      stepList: [],
      loading: false,
      list: [],
      searchText: "",
      tag: [],
      tags: [],
      tagList: [],
      formSubmitting: false,
      text: "",
      title: "",
      validationStatus: true,
      //this is for card
      imgHover: false,
      loadingOverlay:false,
      card: {
        photo: "",
        title: "Enter title",
        text: "Enter your text...",
        buttons: []
      },
      buttonListInCard: [],
      buttonInCard: {
        type: "openUrl",
        text: "Click to edit",
        value: "www.facebook.com"
      },
      photoUrlForCard: "",
      isTitleActive: false,
      isTextActive: false,
      isAddButtonActive: false,
      selected: false,
      activeID: 0
    };
  },
  computed: {
    getAnswerSet() {
      return this.answerSet;
    },
    getActiveAnswersetMsg() {
      return this.activeAnswersetMsg;
    },
    //this is for card
    getCard() {
      return this.card;
    },
    getButtonInCard() {
      return this.buttonListInCard;
    }
    //end for card
  },
  methods: {
    showSelected(a) {
      this.selected = true;
      this.activeID = a.ID;
      this.activeAnsset.splice(0, 1);
      this.activeAnsset.push(a);
    },
    deleteMsg(a) {
      this.activeAnswerset = a;
      this.confirmDel();
    },
    activeSelected(id) {
      return this.activeID == id ? true : false;
    },
    //this is for card
    addNewCard() {
      if (!this.activeAnswerset.ID) {
        this.$modal.show("cardModal");
      } else {
        this.$modal.show("cardModal");
      }
      //console.log("answerSet=", JSON.stringify(this.answerSet));
    },
    mouseHoverinImgDiv() {
      this.imgHover = true;
    },
    mouseLeaveinImgDiv() {
      this.imgHover = false;
    },
    addCardImage() {
      this.$refs.cardImageInput.click();
      //this.card.photo = "http://1.bp.blogspot.com/-8PfnHfgrH4I/TylX2v8pTMI/AAAAAAAAJJ4/TICBoSEI57o/s1600/search_by_image_image.png";
    },
    onCardImageChange(e) {
      const file = e.target.files[0];
      let imageurl = URL.createObjectURL(file);
      this.photoUrlForCard = file;
      //console.log(imageurl);
      this.card.photo = imageurl;
    },
    removeCardImage() {
      this.card.photo = "";
    },
    addNewButtonInCard() {
      this.buttonListInCard.push({ ...this.buttonInCard });
    },
    removeCardBtn(index) {
      this.buttonListInCard.splice(index, 1);
    },
    createCard() {
      this.card.buttons = [...this.buttonListInCard];
      let finalCard = { ...this.card };
      if (finalCard.photo != "") {
        let a;
        let reader = new FileReader();
        reader.readAsDataURL(this.photoUrlForCard);
        reader.onload = () => {
          a = reader.result;
          finalCard.photo = a;
        };
      }
      if (!this.activeAnswerset.ID) {
        this.answerSet.push({
          type: "card",
          text: "",
          url: "",
          cardContent: finalCard
        });
      } else {
        this.activeAnswersetMsg.push({
          type: "card",
          text: "",
          url: "",
          cardContent: finalCard
        });
      }
      //console.log(JSON.stringify(this.card));
      this.card.photo = "";
      this.card.title = "Enter title";
      this.card.text = "Enter your text...";
      this.card.buttons = [];
      this.buttonListInCard = [];
      //console.log(JSON.stringify(this.card));
      //console.log(JSON.stringify(this.buttonListInCard));
      this.$modal.hide("cardModal");
    },

    //end for card

    getImageForModal(url) {
      //return URL.createObjectURL(url);
      return url;
    },
    addNewImage() {
      this.$refs.fileInput.click();
    },
    onChange(e) {
      let fileList = e.target.files;
      let imageUrls = [];

      for (let i = 0; i < fileList.length; i++) {
        let file = e.target.files[i];

        let a;
        let imageurl = URL.createObjectURL(file);
        let reader = new FileReader();

        reader.readAsDataURL(file);

        reader.onload = () => {
          a = reader.result;
          imageUrls.push(a);
          //console.log("imageUrls in innner=",JSON.stringify(imageUrls));
        };
      }
      //console.log("imgurls in outer =",imageUrls)

      if (!this.activeAnswerset.ID) {
        this.answerSet.push({
          type: "image",
          text: "",
          url: imageUrls
        });
        //this.$refs.answersetDiv.scrollIntoView(false);
        //this.$refs.answersetDiv.scrollTop = this.$refs.answersetDiv.scrollHeight;
        //console.log("in if =",JSON.stringify(this.answerSet))
      } else {
        this.activeAnswersetMsg.push({
          type: "image",
          text: "",
          url: imageUrls
        });
        //this.$refs.activeAnswersetDiv.scrollIntoView(false);
        //this.$refs.activeAnswersetDiv.scrollTop = this.$refs.activeAnswersetDiv.scrollHeight;
      }
      // console.log(this.answerSet)
      // console.log(JSON.stringify(this.answerSet))
    },
    addNewText() {
      if (!this.activeAnswerset.ID) {
        this.answerSet.push({
          type: "text",
          text: ""
        });
        //this.$refs.answersetDiv.scrollIntoView(false);
      } else {
        this.activeAnswersetMsg.push({
          type: "text",
          text: ""
        });
        //this.$refs.activeAnswersetDiv.scrollIntoView(false);
        //this.$refs.activeAnswersetDiv.scrollTop = this.$refs.activeAnswersetDiv.scrollHeight;
      }
    },
    // scrollToEnd(){
    //   let container = this.$refs.answersetDiv;
    //   container.scrollTop = container.scrollHeight;
    // },
    remove(index) {
      if (this.activeAnswerset.ID) {
        this.activeAnswersetMsg.splice(index, 1);
      } else {
        this.answerSet.splice(index, 1);
      }
    },
    async getData() {
      try {
        const res = await axios.get("settings/answersetPreData");
        this.channelList = res.data.channels;
        //console.log("channelList="+this.channelList);
        this.stepList = res.data.steps;
        this.tagList = res.data.tags;
      } catch {
        return null;
      }
    },
    async getEditData() {
      try {
        var res = await axios.get(
          `answerset/getEditData?ID=${this.activeAnswerset.ID}`
        );
        this.tags = JSON.parse(res.data);
        //console.log(res.data);
      } catch {
        return null;
      }
    },
    async update() {
      this.channels = this.channel;
      this.steps = this.step;
      // this.tags = this.tag;
      this.selected = false;
      let stepId = this.step.id ? this.step.id : 0;
      let channelId = this.channel.id ? this.channel.id : 0;
      let override = stepId == 0 && channelId == 0;
      if ((this.channel.id && this.step.id) || override) {
        try {
          this.loadingOverlay = true;
          this.loading=true;
          var res = await axios.get(
            `answerset/getAnswersetList?StepID=${stepId}&ChannelID=${channelId}&text=${this.searchText}`
          );
          if (res.status == 200) {
            this.list = res.data.map(x => {
              // x.tags = JSON.parse(x._Tags);
              return x;
            });
          }
        } catch {
        } finally {
          this.loading=false;
          this.loadingOverlay = false;
        }
      }
    },
    async submitAdd() {
      let checkText = true;
      let answerSetText = this.answerSet.filter(x => x.type == "text");
      for (let i = 0; i < answerSetText.length; i++) {
        if (answerSetText[i].text == "") {
          checkText = false;
        }
      }

      let channelIDs;
      if (Array.isArray(this.channels)) {
        channelIDs = this.channels.map(c => c.id);
      } else {
        channelIDs = [this.channels.id];
      }

      let stepId = this.step.id ? this.step.id : 0;

      if (!Array.isArray(this.tags)) {
        this.tags = [this.tags];
      }

      this.checkValidation(
        this.title,
        channelIDs,
        this.tags,
        stepId,
        checkText,
        this.answerSet
      );

      if (this.validationStatus) {
        this.formSubmitting = true;
        try {
          var res = await axios.post("answerset/addMessages", {
            _Tags: JSON.stringify(this.tags),
            Messages: JSON.stringify(this.answerSet),
            AdminID: 1,
            StepID: stepId,
            ChannelID: JSON.stringify(channelIDs),
            Title: this.title
          });
          if (res.status == 200) {
            let answersets = res.data;
            answersets.tags = JSON.parse(answersets._Tags);
            this.list = [answersets, ...this.list];
            this.answerSet = [];
            this.$modal.hide("add");
          }
        } catch {
          this.formSubmitting = false;
        }
      } else {
        this.ForValidation("Unknown Error in SubmitAdd!");
        this.formSubmitting = false;
      }
    },

    checkValidation(title, channelids, tags, step, checkText, checkAnswersets) {
      if (title == "") {
        this.ForValidation("Please enter Title!");
      }
      else if(this.showChannel){
        if(channelids.length < 0)
        this.ForValidation("Please Choose Channel!");
      }else if(this.showStep){
        this.ForValidation("Please Choose Customer Journey Step!");
      }
      // else if(this.showChannel && this.showStep){
      //   if (
      //     channelids.length == 0 &&
      //     tags.length == 0 &&
      //     step == undefined) {
      //     this.ForValidation(
      //       "Please select Channel and Customer Journey Step!"
      //     );
      //   }
      // }


      // else if (channelids != 0 &&  step == undefined) {
      //   this.ForValidation(
      //     "Please select Customer Journey Step and Answerset Tags!"
      //   );
      // } else if (channelids == 0 && step == undefined) {
      //   this.ForValidation("Please select Channel and Customer Journey Step!");
      // } else if (channelids == 0 && step != undefined) {
      //   this.ForValidation("Please select Channel and Answerset Tags!");
      // } else if (channelids == 0 && step != undefined) {
      //   this.ForValidation("Please select Channel!");
      // // } else if (channelids != 0 && tags.length == 0 && step != undefined) {
      // //   this.ForValidation("Please select Answerset Tags!");
      // } else if (channelids != 0 && step == undefined) {
      //   this.ForValidation("Please select Customer Journey Step!");
      // }
      else if (!(checkAnswersets.length > 0)) {
        this.ForValidation("Please add text or image!");
      } else if (checkText == false) {
        this.ForValidation("Please enter text in textboxes!");
      } else {
        this.validationStatus = true;
      }
    },
    ForValidation(txt) {
      this.$modal.show("dialog", {
        text: txt,
        buttons: [{ title: "OK" }, { title: "Close" }]
      });
      this.validationStatus = false;
      this.formSubmitting = false;
    },
    async submitEdit() {
      let checkText = true;
      let msg = this.activeAnswersetMsg.filter(x => x.type == "text");
      for (let i = 0; i < msg.length; i++) {
        if (msg[i].text == "") {
          checkText = false;
        }
      }
      let channelIDs;
      if (Array.isArray(this.channels)) {
        channelIDs = this.channels.map(c => c.id);
      } else {
        channelIDs = [this.channels.id];
      }

      if (!Array.isArray(this.tags)) {
        this.tags = [this.tags];
      }

      // console.log(this.steps)
      // console.log(JSON.stringify(this.steps))
      // console.log(this.steps.id)

      this.checkValidation(
        this.title,
        channelIDs,
        this.tags,
        this.steps.id,
        checkText,
        this.activeAnswersetMsg
      );

      if (this.validationStatus) {
        this.formSubmitting = true;
        this.activeAnswerset.Title = this.title;
        this.activeAnswerset.ChannelID = JSON.stringify(channelIDs);
        this.activeAnswerset.StepID = this.step.id;
        this.activeAnswerset._Tags = JSON.stringify(this.tags);
        this.activeAnswerset.Messages = JSON.stringify(this.activeAnswersetMsg);

        try {
          // console.log(this.activeAnswerset)
          // console.log(JSON.stringify(this.activeAnswerset))
          var res = await axios.post(
            "answerset/updateAnswerset",
            this.activeAnswerset
          );
          if (res.status == 200) {
            //let answersets = res.data;
            //console.log(res.data);
            //console.log(JSON.stringify(res.data))
            this.tags = this.tag;
            this.chanels = this.channel;
            this.steps = this.step;
            this.$modal.hide("add");
          }
        } catch {
          this.formSubmitting = false;
        }
      } else {
        this.ForValidation("Unknown error in submitEdit!");
        this.formSubmitting = false;
      }
    },
    confirmDel() {
      this.$modal.show("dialog", {
        text: `Are you sure you want to delete this answerset?`,
        buttons: [
          { title: "Close" },
          {
            title: "Delete",
            class: "vue-dialog-button bg-danger text-white",
            handler: () => {
              this.$modal.hide("dialog");
              this.submitDel();
            }
          }
        ]
      });
    },
    async submitDel() {
      try {
        this.formSubmitting = true;
        var res = await axios.delete(
          "answerset/del?ID=" + this.activeAnswerset.ID
        );
        if (res.status == 200) {
          let i = this.list.findIndex(x => x.ID === this.activeAnswerset.ID);
          this.list.splice(i, 1);
          this.$modal.hide("add");
          this.selected = false;
        }
      } catch {
        this.formSubmitting = false;
      }
    },
    checkNewTag(v) {
      return this.tagList.find(x => x.Name == v) != undefined
        ? v
        : "Add New Tag: " + v;
    },
    showAddModal() {
      this.title = "";
      this.tags = this.tag;
      this.steps = this.step;
      this.channels = this.channel;
      this.activeAnswerset = {};
      this.searchText = "";
      this.formSubmitting = false;
      this.$modal.show("add");
    },
    showEditModal(a) {
      this.activeAnswerset = a;
      this.title = a.Title;
      //console.log("a=",this.activeAnswerset);
      this.activeAnswersetMsg = JSON.parse(this.activeAnswerset.Messages);
      this.getEditData();
      this.formSubmitting = false;
      this.$modal.show("add");
    },
    isEventFromTagEnter(e) {
      // console.log(e);
      return e.explicitOriginalTarget.classList.contains("vs__search");
    },
    imgUrl(u) {
      if (u.includes("base64")) return u;
      return "https://kktstroage.azureedge.net/yammo/smm/answerset/" + u;
    }
  },
  mounted() {
    this.getData();
    if((!this.showChannel) && (!this.showStep)){
      this.update();
    }
  },
  watch: {
    channel() {
      this.update();
    },
    step() {
      this.update();
    },
    tag() {
      this.update();
    },
    searchText() {
      this.update();
    }
    // tags() {
    //   this.tagList = this.tagList.filter(x => !this.tags.includes(x));
    // }
  }
};
</script>
